<template>
	<view class="container">
		<canvas canvas-id='canvasid' 
		:class="[canvas , debug ? 'debug' : 'pro']" 
		:style="{width: pxWidth + 'px', height: pxHeight + 'px'}">
		</canvas>
	</view>
</template>

<script>
	import { main, handle, helper} from './unit.js'
	export default {
		data() {
			return {
				pxWidth:'',
				pxHeight:''
			}
		},
		properties: {},
		onLoad() {
			const sysInfo = wx.getSystemInfoSync();
			const screenWidth = sysInfo.screenWidth;
			this.factor = screenWidth / 750;
		},
		methods: Object.assign({
			/**
			 * 计算画布的高度
			 * @param {*} config
			 */
			getHeight(config) {
				const getTextHeight = (text) => {
					let fontHeight = text.lineHeight || text.fontSize;
					let height = 0;
					if (text.baseLine === 'top') {
						height = fontHeight;
					} else if (text.baseLine === 'middle') {
						height = fontHeight / 2;
					} else {
						height = 0;
					}
					return height;
				}
				const heightArr = [];
				(config.blocks || []).forEach((item) => {
					heightArr.push(item.y + item.height);
				});
				(config.texts || []).forEach((item) => {
					let height;
					if (Object.prototype.toString.call(item.text) === '[object Array]') {
						item.text.forEach((i) => {
							height = getTextHeight({ ...i,
								baseLine: item.baseLine
							});
							heightArr.push(item.y + height);
						});
					} else {
						height = getTextHeight(item);
						heightArr.push(item.y + height);
					}
				});
				(config.images || []).forEach((item) => {
					heightArr.push(item.y + item.height);
				});
				(config.lines || []).forEach((item) => {
					heightArr.push(item.startY);
					heightArr.push(item.endY);
				});
				const sortRes = heightArr.sort((a, b) => b - a);
				let canvasHeight = 0;
				if (sortRes.length > 0) {
					canvasHeight = sortRes[0];
				}
				if (config.height < canvasHeight || !config.height) {
					return canvasHeight;
				} else {
					return config.height;
				}
			},
			create(config) {
				this.ctx = wx.createCanvasContext('canvasid', this);

				this.pixelRatio = config.pixelRatio || 1;
				const height = this.getHeight(config);
				this.initCanvas(config.width, height, config.debug)
					.then(() => {
						// 设置画布底色
						if (config.backgroundColor) {
							this.ctx.save();
							this.ctx.setFillStyle(config.backgroundColor);
							this.ctx.fillRect(0, 0, this.toPx(config.width), this.toPx(height));
							this.ctx.restore();
						}
						const {
							texts = [], images = [], blocks = [], lines = []
						} = config;
						const queue = this.drawArr
							.concat(texts.map((item) => {
								item.type = 'text';
								item.zIndex = item.zIndex || 0;
								return item;
							}))
							.concat(blocks.map((item) => {
								item.type = 'block';
								item.zIndex = item.zIndex || 0;
								return item;
							}))
							.concat(lines.map((item) => {
								item.type = 'line';
								item.zIndex = item.zIndex || 0;
								return item;
							}));
						// 按照顺序排序
						queue.sort((a, b) => a.zIndex - b.zIndex);

						queue.forEach((item) => {
							if (item.type === 'image') {
								this.drawImage(item)
							} else if (item.type === 'text') {
								this.drawText(item)
							} else if (item.type === 'block') {
								this.drawBlock(item)
							} else if (item.type === 'line') {
								this.drawLine(item)
							}
						});

						const res = wx.getSystemInfoSync();
						const platform = res.platform;
						let time = 0;
						if (platform === 'android') {
							// 在安卓平台，经测试发现如果海报过于复杂在转换时需要做延时，要不然样式会错乱
							time = 300;
						}
						this.ctx.draw(false, () => {
							setTimeout(() => {
								wx.canvasToTempFilePath({
									canvasId: 'canvasid',
									success: (res) => {
										// this.triggerEvent('success', res.tempFilePath);
										this.$emit('success', res.tempFilePath)
									},
									fail: (err) => {
										// this.triggerEvent('fail', err);
										this.$emit('fail', err)
									},
								}, this);
							}, time);
						});
					})
					.catch((err) => {
						wx.showToast({
							icon: 'none',
							title: err.errMsg || '生成失败'
						});
						console.error(err);
					});
			},
		}, main, handle, helper),
	}
</script>

